import SetBirthday from '@/pages/setUserData/components/setBirthday'
// 
import SetTall from '@/pages/setUserData/components/setTall'
import SetPhone from '@/pages/setUserData/components/setPhone'
// 
import Transcation from '@/pages/setting/transcation'
// 
import LikeDetail from '@/pages/setting/likeDetail'
// 
import UploadCert from '@/pages/Auth/uploadCert'
// 
import UploadEducation from '@/pages/Auth/uploadEducation'
// 
import UploadCareer from '@/pages/Auth/uploadCareer'
// 404
import NotFound from '@/pages/NotFound'
// 
import Test from '@/pages/test'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    let position = null
    // 
    if (savedPosition) {
      position = savedPosition
    } else {
      // meta
      // 
      if (to.meta.scrollTop === false) {
        position = { x: 0, y: to.meta.scrollHeight }
      } else {
        position = { x: 0, y: 0 }
      }
    }
    return position
  },
  routes: [
    {
      name: '',
      path: '',
      component: Index,
      children: [
        {
          name: 'Home',
          path: '/',
          alias: '/index.html',
          component: Home,
          meta: {
            isHideHeader: true
          }
        },
        {
          name: 'Messages',
          path: '/messages.html',
          component: Message,
          meta: {
            isHideHeader: true
          }
        },
        {
          name: 'Account',
          path: '/account.html',
          component: Account,
          meta: {
            isHideHeader: true
          }
        }
      ]
    },
    {
      name: 'combo',
      path: '/combo.html',
      component: Combo,
      meta: {
        title: ''
      }
    },
    {
      name: 'PersonInfo',
      path: '/info-:id.html',
      component: PersonInfo,
      meta: {
        scrollTop: true
      }
    },
    {
      name: 'UserDeal',
      path: '/userDeal.html',
      component: UserDeal
    },
    {
      name: 'Agreement',
      path: '/agreement.html',
      component: Agreement
    },
    {
      name: 'AboutUs',
      path: '/aboutus.html',
      component: AboutUs
    },
    {
      name: 'Like',
      path: '/like.html',
      component: Like,
      meta: {
        title: '',
        needLogin: true
      }
    },
    {
      name: 'LikeMe',
      path: '/likeme.html',
      component: LikeMe,
      meta: {
        title: '',
        needLogin: true
      }
    },
    {
      name: 'Follow',
      path: '/follow.html',
      component: Follow,
      meta: {
        title: '',
        needLogin: true
      }
    },
    {
      name: 'Ask',
      path: '/ask.html',
      component: Ask,
      meta: {
        title: '',
        needLogin: true
      }
    },
    {
      name: 'MessageDetail',
      path: '/message-detail-:id.html',
      component: MessageDetail
    },
    {
      name: 'Setting',
      path: '/setting.html',
      component: Setting,
      meta: {
        title: ''
      }
    },
    {
      name: 'CustomerService',
      path: '/customer-service.html',
      component: CustomerService,
      meta: {
        title: ''
      }
    },
    {
      name: 'SetUserData',
      path: '/setUserData',
      component: SetUserData,
      meta: {
        needLogin: true
      },
      children: [
        {
          name: 'SetSex',
          path: 'sex.html',
          component: SetSex,
          meta: {
            title: ''
          }
        },
        {
          name: 'SetEducation',
          path: 'education.html',
          component: SetEducation,
          meta: {
            title: ''
          }
        },
        {
          name: 'SetBirthday',
          path: 'birthday.html',
          component: SetBirthday,
          meta: {
            title: ''
          }
        },
        {
          name: 'SetTall',
          path: 'tall.html',
          component: SetTall,
          meta: {
            title: ''
          }
        },
        {
          name: 'SetPhone',
          path: 'phone.html',
          component: SetPhone,
          meta: {
            title: ''
          }
        }
      ]
    },
    {
      name: 'BaseInfo',
      path: '/baseInfo.html',
      component: BaseInfo,
      meta: {
        title: '',
        needLogin: true
      }
    },
    {
      name: 'Transcation',
      path: '/transcation.html',
      component: Transcation,
      meta: {
        title: ''
      }
    },
    {
      name: 'LikeDetail',
      path: '/likeDetail.html',
      component: LikeDetail,
      meta: {
        title: ''
      }
    },
    {
      name: 'UploadCert',
      path: '/upload-cert.html',
      component: UploadCert,
      meta: {
        title: ''
      }
    },
    {
      name: 'UploadEducation',
      path: '/upload-education.html',
      component: UploadEducation,
      meta: {
        title: ''
      }
    },
    {
      name: 'UploadCareer',
      path: '/upload-career.html',
      component: UploadCareer,
      meta: {
        title: ''
      }
    },
    {
      name: 'Test',
      path: '/test.html',
      component: Test,
      meta: {
        title: 'cropper'
      }
    },
    {
      name: 'NotFound',
      path: '*',
      component: NotFound
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (from.name) {
    Storage.save('referer', from.fullPath)
  } else {
    Storage.remove('referer')
  }
  // 
  if (from.meta.scrollTop === false) {
    const scrollHeight = window.pageYOffset || document.documentElement.scrollTop
    from.meta.scrollHeight = scrollHeight
  }
  if (store.getters.userId) {
    if ((to.name === 'Account' || to.name === 'BaseInfo') && store.state.userData.status === 0) {
      next({
        path: '/setUserData/sex.html'
      })
    } else {
      next()
    }
  } else if (to.meta.needLogin && !getCookie('session')) {
    next({
      path: '/account.html',
      query: {
        direct: to.fullPath
      }
    })
  } else {
    if (!to.meta.needLogin && getCookie('session')) {
      store._actions.fetchUserData[0]()
      next()
    } else {
      store._actions.fetchUserData[0]().then(res => {
        if ((to.name === 'Account' || to.name === 'BaseInfo') && store.state.userData.status === 0) {
          next({
            path: '/setUserData/sex.html'
          })
        } else {
          next()
        }
      }, () => {
        if (from.name && from.name !== 'Account') {
          next({
            path: '/account.html',
            query: {
              direct: to.fullPath
            }
          })
        } else {
          next()
        }
      }).catch(err => {
        alert(err)
      })
    }
  }
})

export default router
