<template>
  <div>
    <slot></slot>
  </div>
</template>

<script>
import qs from 'qs'
import { Toast } from 'vant'
import { isIOS, transferBytes } from '@/utils/tools'
import OSS from 'ali-oss'
const getRotate = () => {
  return import('@/utils/rotateImg.js')
}
const abtest = true

let accessid = ''
let host = ''
let policyBase64 = ''
let signature = ''
let callbackbody = ''
let callbackVar = ''
let filename = ''
let key = ''
let expire = 0
let objectName = ''
let bucket = ''
let accessKeySecret = ''
let stsToken = ''
let objectKey = ''
let callbackUrl = ''
let callbackContentType = ''
export default {
  props: [
    'container',
    'browse_button',
    'actionFile',
    'params',
    'args',
    'isClip'
  ],
  watch: {
    async actionFile (val) {
      if (val) {
        if (abtest) {
          this.set_upload_param('')
          let obj = {
            region: 'oss-cn-hangzhou',
            //AccessKeyAPISTSAPI
            accessKeyId: accessid,
            accessKeySecret: accessKeySecret,
            stsToken: stsToken,
            bucket: bucket
          }
          const client = new OSS(obj)

          Toast.loading({
            duration: 0,
            forbidClick: true,
            message: ''
          })

          const options = {
            partSize: 500 * 1024,
            meta: {
              year: 2020,
              people: ''
            },
            callback: {
              url: callbackUrl,
              body: callbackbody,
              contentType: callbackContentType,
              customValue: callbackVar
            }
          }

          let { data } = await client.multipartUpload(objectKey, val, options).catch((err) => {
            this.$toast('')
          }).finally(() => {
            Toast.clear()
          })

          if (data.status !== 'OK') return

          let picId = data.picId

          this.$http(`/api/member/image/url`, { picId }, 'get').then(res => {
            if (res.code === '0000') {
              let { url } = res.data
              this.$emit('uploadSucess', {
                ...res.data,
                args: this.args
              })
            }
          })
        } else {
          let params = {
            ...this.set_upload_param(''),
            file: val
          }
          let arr = Object.keys(params)
          let formData = new FormData()
          
          Toast.loading({
            duration: 0,
            forbidClick: true,
            message: ''
          })

          arr.forEach(element => formData.append(element, params[element]))
          let picId = null
          // this.$http('http://hello-image-test.oss-cn-hangzhou.aliyuncs.com/', formData).then(info => {
          this.$http(host + '/', formData).then(info => {
            if (info.status === 'OK') {
              picId = info.picId

              this.$http(`/api/member/image/url`, { picId }, 'get').then(res => {
                if (res.code === '0000') {
                  let { url } = res.data
                  this.$emit('uploadSucess', {
                    ...res.data,
                    args: this.args
                  })
                }
              })
            }
          }, err => {
            this.$toast('upload error')
          }).finally(() => {
            Toast.clear()
          })
        }
        
      }
    }
  },

  methods: {

    set_upload_param (filename) {
      this.get_signature()
      objectName = key

      let newMultipartParams = {
        'key': objectName,
        'policy': policyBase64,
        'OSSAccessKeyId': accessid,
        'success_action_status': '200', // 200,204
        'callback': callbackbody,
        'signature': signature,
        ...callbackVar
      }
      return newMultipartParams
    },
    sendRequest () {
      var xmlhttp = null
      if (window.XMLHttpRequest) {
        xmlhttp = new XMLHttpRequest()
      } else if (window.ActiveXObject) {
        // eslint-disable-next-line no-undef
        xmlhttp = new ActiveXObject('Microsoft.XMLHTTP')
      }
      if (xmlhttp != null) {
        let params = qs.stringify(this.params || {})
        let serverUrl = ''
        if (abtest) {
          serverUrl = `api/member/image/upload/sts?${params}`
        } else {
          serverUrl = `/api/member/image/upload/policy?${params}`
        }

        xmlhttp.open('GET', serverUrl, false)
        xmlhttp.send(null)
        return xmlhttp.responseText
      } else {
        console.log('Your browser does not support XMLHTTP.')
      }
    },

    get_signature () {
      // expire  3s 
      let body = this.sendRequest()
      let data = JSON.parse(body)
      var obj = data.data
      host = obj['host']
      policyBase64 = obj['policy']
      accessid = obj['accessKeyId']
      signature = obj['signature']
      expire = parseInt(obj['expire'])
      callbackbody = obj['callbackBody']
      callbackVar = obj['callbackVar']
      key = obj['key']
      bucket = obj['bucket']
      accessKeySecret = obj['accessKeySecret']
      stsToken = obj['stsToken']
      objectKey = obj['objectKey']
      callbackUrl = obj['callbackUrl']
      callbackContentType = obj['callbackContentType']
    }
  },
  mounted () {
    let _this = this
    this.$nextTick(e => {
      let container = document.getElementById(this.browse_button)
      if (!container) return
      container.style.cssText = 'position: relative;overflow: hidden;display:block;z-index: 5;'

      let input = document.createElement('input')
      input.setAttribute('type', 'file')
      input.setAttribute('id', this.browse_button)
      input.setAttribute('accept', 'image/*')
      input.setAttribute('class', 'hideInputFile')

      input.addEventListener('change', (e) => {
        let target = e.target
        let file = target.files[0]

        /* if (isIOS() && !this.isClip) {
          return getRotate().then(({ rotatePhoto }) => {
            rotatePhoto(file, (res) => {
              this.$emit('fileChange', {
                file: res,
                args: this.args
              })
            })
          })
        } */
        this.$emit('fileChange', {
          file: file,
          args: this.args
        })
      }, false)
      container.appendChild(input)
      // console.log('container===?', container)
    })
  }
}
</script>

<style lang="scss">
.hideInputFile {
  font-size: 12px;
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 10;
}
</style>
