<template>
  <div class="set-phone">
    <p class="title"></p>

    <p class="notice"></p>

    <div class="tab">
      <div class="tab-con">
        <p class="tab-item"></p>
      </div>
      <i class="active"></i>
    </div>


    <div>
      <div class="input_phone">
        <input type="tel" v-model="phone" class="input" placeholder="" />
        <van-count-down
          use-slot
          :time="time"
        >
          <button  class = "btn" @click="getCode" :disabled="ifSend" >{{btnText}}
            <van-count-down class="count" :auto-start="false"  :time="time" format="ss " ref="countdown"
                            @finish="finished()" v-show="ifSend == true" /></button>
        </van-count-down>

      </div>

      <div class="input_code">
        <input type="tel" v-model="code" class="input" placeholder="" />
      </div>
    </div>
    <p class="wrong" v-if="ifMatch === false"></p>
    <div class="complet">
    <a class="content-btn selected"  @click="complete()"></a>
      </div>

    <baseNoticeModal :show="isShowModal" @close="isShowModal=false" @callback="isShowModal=false" />
  </div>

</template>

<script>
  import { mapState, mapMutations } from 'vuex'
  import { setCookie, getCookie } from '@/utils/tools'
  import { CountDown } from 'vant'
  import baseNoticeModal from '@/components/common/baseNoticeModal'
  export default {
    components: {
      baseNoticeModal
    },
    computed: {
      ...mapState({
        userData: 'userData'
      })
    },
    data () {
      return {
        phone: '',
        code: "",
        ifMatch: true,
        ifSend: false,
        btnText: "",
        time: 60 * 1000,
        isShowModal: false
      }
    },
    watch: {
      userData: {
        handler (val) {
          this.phone = val.mobile
        },
        immediate: true
      }
    },
    methods: {
      ...mapMutations({
        updateUserData: 'updateUserData'
      }),
      initCountdown() {
        this.ifSend = false;
        this.btnText = "";
        this.$refs.countdown.reset()
      },
      finished() {
        this.initCountdown()
      },
      countStart(){
        const countDown = this.selectComponent('.count');
        countDown.start();
      },
      getCode(){
        if (!this.phone) {
          return this.$toast('')
        }
        let tempData = {}
        tempData.mobile = this.phone
        this.$root.openLoading()
        this.$http(`/api/member/sms/sendCode`,
          tempData
        ).then(res => {
          this.$root.closeLoading()
          if (res.code === '0000') {
            this.ifSend = true
            this.btnText = " "
            this.$refs.countdown.start()
          } else {
            this.$toast(res.msg)
          }
        }, err => {
          this.$toast(err)
          this.$root.closeLoading()
        })
      },
      complete(){
        if (!this.phone) {
          return this.$toast('')
        }
        if (!this.code) {
          return this.$toast('')
        }
        this.$root.openLoading()
        this.$http('/api/member/sms/checkCode', {
          mobile: this.phone,
          code: this.code,
          type: 1
        }, 'get').then(res => {
          this.$root.closeLoading()
          if (res.code === '0000') {
            if (res.data.result === 1) {
              this.updateUserData({
                mobild: this.phone,
                ifMobileVerify: 1
              })
              this.isShowModal = true
//              this.$router.replace('/account.html')
            }else {
              this.ifMatch = false
            }
          } else {
            this.ifMatch = false
            this.$toast(res.msg)
          }
        }, (res) => {
          this.$toast('')
          this.ifMatch = false
          this.$root.closeLoading()
        })
      }
    }
  }
</script>
