        <p class="text-num">{{infoData.selfDescription.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.familyInfo" placeholder=""
          :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.familyInfo.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
