        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.personalExperience"
          placeholder="" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.personalExperience.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.hobby" placeholder=""
          :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.hobby.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title"></p>
        <textarea class="textarea" :readonly="!isEditable" v-model="infoData.loveMind" placeholder=""
          :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.loveMind.length}}/{{limitLength}}</p>
