          </dl>
          <!-- <a href="javascript:;" class="upload-btn common-bg">
            
            <input type="file" class="upload-input" accept="image/*" @change="uploadWebchatCode" />
          </a> -->

          <Oss @uploadSucess="WebchatCodeSuccess" browse_button="uploadWebchatCodeBtn1" @fileChange="selectedWxCode"
            :actionFile="wxCodeFile" container="wxCodeContainer1" :params='wxCodeParams'>
            <div id="wxCodeContainer1">
              <span id="uploadWebchatCodeBtn1" class="upload-btn common-bg">
                
              </span>
            </div>
          </Oss>

        </template>
        <template v-else>
          <img class="code-img" v-lazy="infoData.wechatPicUrl" :key="infoData.wechatPicUrl" alt="" />

          <!-- <a href="javascript:;" class="reupload-btn" v-if="isEditable">
            
            <input type="file" class="upload-input" accept="image/*" @change="uploadWebchatCode" />
          </a> -->

          <Oss v-if="isEditable && !isDisable" @uploadSucess="WebchatCodeSuccess" browse_button="uploadWebchatCodeBtn2"
            @fileChange="selectedWxCode" :actionFile="wxCodeFile" container="wxCodeContainer2" :params='wxCodeParams'>
            <div id="wxCodeContainer2">
              <span id="uploadWebchatCodeBtn2" class="reupload-btn">
                
              </span>
            </div>
          </Oss>
        </template>
      </div>
    </van-popup>

    <Clip v-model="isShowClip" :img="tempImg" @clipComplete="clipComplete" />

    <van-image-preview v-if="infoData.coverPicUrlList" v-model="isShowPreview" :images="infoData.coverPicUrlList" :start-position="previewIndex"></van-image-preview>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import Oss from '@/components/common/oss'
import Clip from '@/components/common/clip'
import AccelerateAduit from '@/components/dialog/accelerateAduit'
import qs from 'qs'
import { UPLOAD_TYPE } from '@/config/constant'

import { isIOS, transferBytes, watermark } from '@/utils/tools'
import Storage from '@/utils/Storage'
import wxApi from '@/utils/wxAuth'
const getRotate = () => {
  return import('@/utils/rotateImg.js')
}

export default {
  name: 'BaseInfo',
  components: {
    Clip,
    AccelerateAduit,
    Oss
  },
  computed: {
    ...mapState({
      userData: 'userData',
      sexMap: 'sexMap',
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap',
      areaData: 'areaData'
    }),
    filterAreaData () {
      let a = JSON.parse(JSON.stringify(this.areaData))
      delete a.province_list['100000']
      delete a.city_list['100100']
      return a
    },
    ...mapGetters({
      userId: 'userId'
    }),
    isDisable () {
      if (this.isUpdateInfo) {
        return false
      }
      if ([3, 1].includes(parseInt(this.userData.basedataAuth))) {
        return true
      }
      return false
    },
    // eslint-disable-next-line vue/return-in-computed-property
    submitLabel () {
      if (this.isUpdateInfo) return ''
      let str = ''
      switch (this.userData.basedataAuth) {
        case 3:
          str = ''
          break
        case 1:
          str = ''
          break
        default:
          str = ''
          break
      }
      return str
    },
    isEditable () {
      return parseInt(this.userData.basedataAuth) !== 1
    },
    salaryArr () {
      let arr = []
      for (let i in this.salaryMap) {
        arr.push({
          value: i,
          text: this.salaryMap[i]
        })
      }
      // 
      arr.shift(0)
      return arr
    },
    statusArr () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      // 
      arr.shift(0)
      arr.pop()
      arr.pop()
      return arr
    },
    assetsArr () {
      let arr = []
      for (let i in this.assetsMap) {
        arr.push({
          value: i,
          text: this.assetsMap[i]
        })
      }
      return arr
    }
  },
  data () {
    return {
      coverParams: { type: UPLOAD_TYPE.cover },
      wxCodeParams: { type: UPLOAD_TYPE.wxCode },
      coverFile: null, // file
      wxCodeFile: null,
      isUpdateInfo: false,
      areaColumns: 2,
      isNativeForeign: false, // 
      isForeign: false, // 
      showPicker: false,
      pickerType: 1,
      pickerData: [],
      showChatCodePop: false,
      infoData: {
        coverPicList: [],
        coverPicUrlList: [], // oss 
        nickName: '',
        nativeCityId: '',
        nativeCountry: '',
        nativeState: '',
        cityId: '',
        country: '',
        state: '',
        mobile: '',
        wechatPicId: '',
        wechatPicUrl: '',
        school: '',
        ifStudent: 0,
        job: '',
        company: '',
        companyVisible: 1,
        industry: '',
        // 
        annualSalary: 1,
        'nativeLocation': '', // 
        'locationCity': '', // 
        // 
        singleStatus: '',
        // 
        richesInfo: '',
        inviteCode: '',
        selfDescription: '',
        familyInfo: '',
        personalExperience: '',
        hobby: '',
        // 
        loveMind: '',
        // 
        ideaPartner: ''
      },
      limitLength: 500,
      isShowDialog: false,
      isShowClip: false,
      tempImg: '',
      isShowSingleDialog: false,
      isShowPreview: false,
      previewIndex: 0
    }
  },
  watch: {
    '$route' (to, from) {
      if (this.$route.query.upd) {
        this.isUpdateInfo = true
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    // base64
    dataURLtoFile: function (dataurl, filename) {
      var arr = dataurl.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new File([u8arr], filename, { type: mime })
    },

    // 
    selectedImg (e) {
      this.tempImg = e.file
      this.isShowClip = true
    },

    coverPicSuccess ({ picId, url }) {
      if (this.infoData.coverPicUrlList) {
        this.infoData.coverPicUrlList.push(url)
      } else {
        this.infoData.coverPicUrlList = [url]
      }
      if (this.infoData.coverPicList) {
        this.infoData.coverPicList.push(picId)
      } else {
        this.infoData.coverPicList = [picId]
      }
      this.coverFile = null
    },

    WebchatCodeSuccess ({ picId, url }) {
      this.infoData.wechatPicUrl = url
      this.infoData.wechatPicId = picId
      if (this.showChatCodePop) this.showChatCodePop = false
    },

    clipComplete ({ data }) {
      this.coverFile = data
      this.isShowClip = false
    },
    deleteImg (index) {
      this.infoData.coverPicUrlList.splice(index, 1)
      this.infoData.coverPicList.splice(index, 1)
    },

    // 
    selectedWxCode (e) {
      this.wxCodeFile = e.file
    },
    switchEducation () {
      if (!this.isEditable) return
      this.infoData.ifStudent = this.infoData.ifStudent === 1 ? 0 : 1
    },
    switchComVisible () {
      if (!this.isEditable) return
      this.infoData.companyVisible = this.infoData.companyVisible === 1 ? 0 : 1
    },
    switchSalaryVisible () {
      if (!this.isEditable) return
      this.infoData.annualSalaryVisible = this.infoData.annualSalaryVisible === 1 ? 0 : 1
    },
    /**
     * @description picker
     * @param type { Number }
     *    0: 
     *    1: 
     *    2: 
     *    3: 
     *    4: 
     */
    triggerPicker (type) {
      if (!this.isEditable) return
      this.pickerType = type

      switch (type) {
        case 0:
          if (this.infoData.nativeCityId === '900000') {
            this.areaColumns = 1
          } else {
            this.areaColumns = 2
          }
          break
        case 1:
          if (this.infoData.cityId === '900000') {
            this.areaColumns = 1
          } else {
            this.areaColumns = 2
          }
          break
        case 2:
          this.pickerData = this.salaryArr
          break
        case 3:
          this.pickerData = this.statusArr
          break
        case 4:
          this.pickerData = this.assetsArr
          break
      }

      if (type !== 3 || Storage.loadOntime('settingModal')) {
        this.$nextTick(() => {
          this.showPicker = true
        })
      } else {
        Storage.saveOnday('settingModal', true, 30)
        this.isShowSingleDialog = true
      }
    },
    confirmSingleDialog () {
      this.showPicker = true
    },
    handleDialogcancel () {
      this.showPicker = false
    },
    handleAreachange (picker, res) {
      if (res[0].code === '900000') {
        this.areaColumns = 1
      } else {
        this.areaColumns = 2
      }
    },
    handleAreaconfirm (res) {
      if (!res[0].code) {
        this.showPicker = false
        return
      }
      this.showPicker = false

      // this.infoData.provice = res[0]
      // 
      if (res[0].code === '900000') {
        if (this.pickerType === 0) {
          this.isNativeForeign = true
          this.infoData.nativeCityId = res[0].code
          this.infoData.nativeLocation = ''
          // this.infoData.nativeLocation = res[0].name === res[1].name ? res[0].name : `${res[0].name}${res[1].name}`
        } else {
          this.isForeign = true
          this.infoData.cityId = res[0].code
          this.infoData.locationCity = ''
          // this.infoData.locationCity = res[0].name === res[1].name ? res[0].name : `${res[0].name}${res[1].name}`
        }
      } else {
        if (this.pickerType === 0) {
          this.isNativeForeign = false
          this.infoData.nativeCityId = res[1].code
          this.infoData.nativeLocation = res[0].name === res[1].name ? res[0].name : `${res[0].name}${res[1].name}`
        } else {
          this.isForeign = false
          this.infoData.locationCity = `${res[1].name}`
          this.infoData.cityId = res[1].code
        }
      }
    },
    handlePickerConfirm (res) {
      this.showPicker = false
      switch (this.pickerType) {
        case 2:
          this.infoData.annualSalary = res.value
          break
        case 3:
          this.infoData.singleStatus = res.value
          break
        case 4:
          this.infoData.richesInfo = res.value
          break
      }
    },
    /**
     * @description 
     */
    saveDraft () {
      if (this.isDisable) {
        return false
      }
      // 
      let draftData = {
        coverPicList: this.infoData.coverPicList,
        coverPicUrlList: this.infoData.coverPicUrlList,
        name: this.infoData.name,
        nickName: this.infoData.nickName,
        // 
        nativeCityId: this.infoData.nativeCityId,
        nativeCountry: this.infoData.nativeCountry,
        nativeState: this.infoData.nativeState,
        // 
        cityId: this.infoData.cityId,
        country: this.infoData.country,
        state: this.infoData.state,
        mobile: this.infoData.mobile,
        wechatPicId: this.infoData.wechatPicId,
        wechatPicUrl: this.infoData.wechatPicUrl,
        school: this.infoData.school,
        ifStudent: this.infoData.ifStudent,
        job: this.infoData.job,
        company: this.infoData.company,
        companyVisible: this.infoData.companyVisible,
        industry: this.infoData.industry,
        // 
        annualSalary: this.infoData.annualSalary,
        annualSalaryVisible: this.infoData.annualSalaryVisible,
        // 
        singleStatus: this.infoData.singleStatus,
        // 
        richesInfo: this.infoData.richesInfo,
        inviteCode: this.infoData.inviteCode,
        selfDescription: this.infoData.selfDescription,
        familyInfo: this.infoData.familyInfo,
        personalExperience: this.infoData.personalExperience,
        hobby: this.infoData.hobby,
        // 
        loveMind: this.infoData.loveMind,
        // 
        ideaPartner: this.infoData.ideaPartner
      }
      try {
        Storage.saveOnday('draftData', draftData, 7)
        this.$toast('')
      } catch (err) {
        this.$toast(err)
      }
    },
    /**
     * @description 
     */
    confirm () {
      this.isShowDialog = false
      this.$router.back()
    },
    /**
     * @description 
     */
    handleSubmit () {
      // if (this.infoData) {
      //   console.log('this.infoData===>', JSON.stringify(this.infoData, null, 2))
      //   return
      // }

      if (this.isDisable) return
      // 
      let fd = this.infoData
      if (!fd.coverPicList || fd.coverPicList.length === 0) {
        return this.$toast(', ')
      }
      if (!fd.name) {
        return this.$toast('')
      }
      if (!fd.nickName) {
        return this.$toast('')
      }
      if (fd.isNativeForeign) {
        if (!fd.nativeCountry || !fd.nativeState) {
          return this.$toast('')
        }
        fd.nativeLocation = fd.nativeCountry + fd.nativeState
      } else {
        // TODO: 
        if (!fd.nativeCityId) {
          return this.$toast('')
        }
      }
      if (fd.isForeign) {
        if (!fd.country || !fd.state) {
          return this.$toast('')
        }
        fd.locationCity = fd.country + fd.state
      } else {
        // TODO: 
        if (!fd.cityId) {
          return this.$toast('')
        }
      }
      if (!fd.mobile) {
        return this.$toast('')
      }
      if (!fd.wechatPicUrl) {
        return this.$toast('')
      }
      if (!fd.school) {
        return this.$toast('')
      }
      if (!fd.singleStatus) {
        return this.$toast('')
      }
      if (!fd.richesInfo) {
        return this.$toast('')
      }
      if (!fd.ifStudent) {
        if (!fd.job) {
          return this.$toast('')
        }
        if (!fd.company) {
          return this.$toast('')
        }
        if (!fd.industry) {
          return this.$toast('')
        }
        if (!fd.annualSalary) {
          return this.$toast('')
        }
      }
      if (!fd.selfDescription) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.familyInfo) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.personalExperience) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.hobby) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.loveMind) {
        return this.$toast(`${this.limitLength}`)
      }
      if (!fd.ideaPartner) {
        return this.$toast(`${this.limitLength}`)
      }

      // 
      delete fd.identityPicList
      delete fd.identityPicUrlList
      delete fd.identityAuthReason
      delete fd.identityAuthTime
      delete fd.identityCommitTime
      delete fd.workPicList
      delete fd.workPicUrlList
      delete fd.workAuthReason
      delete fd.workAuthTime
      delete fd.workCommitTime
      delete fd.educationPicList
      delete fd.educationPicUrlList
      delete fd.educationAuthReason
      delete fd.educationAuthTime
      delete fd.educationCommitTime

      // 
      this.infoData.basedataAuth = 1
      this.$root.openLoading()
      this.$http('/api/member/save', this.infoData).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000' || res.code === '200003') {
          // TODO: 
          if (res.code === '0000') {
            this.updateUserData(this.infoData)
          } else {
            this.updateUserData({ ...this.infoData, ...this.userData.singleStatus })
          }
          this.isShowDialog = true
          // 
          Storage.remove('draftData')
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    showPreview (index) {
      this.previewIndex = index
      this.isShowPreview = true
    },
    changePreview (index) {
      this.$refs['swipe'].swipeTo(index)
    },
  },
  mounted () {
    if (this.$route.query.upd) {
      this.isUpdateInfo = true
    }
    this.infoData = { ...this.infoData, ...this.userData }
    this.infoData.cityId = '' + (this.infoData.cityId ? this.infoData.cityId : '')
    this.infoData.nativeCityId = '' + (this.infoData.nativeCityId ? this.infoData.nativeCityId : '')
    let draftData = Storage.loadOntime('draftData')
    if (draftData) {
      this.infoData = { ...this.infoData, ...draftData }
    }
    if (this.infoData.cityId === '900000') {
      this.isForeign = true
    } else {
      this.isForeign = false
    }
    if (this.infoData.nativeCityId === '900000') {
      this.isNativeForeign = true
    } else {
      this.isNativeForeign = false
    }
  },
  activated () {
    let d = this.userData
    if (this.userId && parseInt(d.basedataAuth) === 3) {
      wxApi.configShare({
        title: 'Hello',
        // desc: 'ID----',
        desc: `${this.mId}-${this.educationMap[d.education]}-${d.ifStudent ? '' : d.job}-${d.age}-${parseInt(d.cityId) === 900000 ? (d.country + ' ' + d.state) : this.areaData['city_list'][d.cityId]}`,
        link: `${location.origin}/info-${this.userId}.html`,
        imgUrl: `${d.headPicUrl}`

        // imgUrl: `${location.origin}/api/member/pic?id=${d.coverPicList[0]}&type=1&targetMemberId=${d.id}`
      })
    } else {
      wxApi.configShare()
    }
  },
  deactivated () {
    wxApi.configShare()
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (!vm.userData.ifMobileVerify) {
        vm.$router.replace('/setUserData/phone.html')
      }
    })
  }
}
</script>

