<template>
  <div class="detail-info-container">
    <div class="img-swiper" v-if="targetData.coverPicUrlList">
      <Swipe :autoplay="0" indicator-color="white" class="img-box" ref="swipe">
        <SwipeItem class="img-box" v-for="(img, index) in targetData.coverPicUrlList" :key="img" @click="showPreview(index)">
          <img class="img" :src="`${img}`" />
        </SwipeItem>
      </Swipe>
      <span class="img-num">{{targetData.coverPicList.length}}</span>
    </div>
    <div class="info-box" v-if="targetData.id">
      <div class="name-op-con">
        <span class="name-con"><i class="sex-icon" :class="{female: targetData.sex == 2}"></i><span
            class="nick">{{targetData.nickName}}</span><img class="identify-icon" src="~@/assets/img/identify.png"
            v-if="targetData.identityAuth === 3 && targetData.academicAuth === 3 && targetData.workAuth === 3" /></span>
        <span class="op-con" v-if="mId != targetData.mid">
          <a href="javascript:;" class="report btn" @click="switchReport"></a>
          <a href="javascript:;" class="follow btn" :class="{'followed': targetData.follow}"
            @click="switchFollow">{{targetData.follow ? '' : ''}}</a>
        </span>
      </div>
      <div class="id-con" ref="code-con"><strong class="id">ID: {{targetData.mid}}</strong><a href="javascript:;"
          class="copy-btn" @click="execCopy(targetData.mid)"></a></div>
      <p class="info-con">
        {{targetData.age}}&nbsp;{{calculater(targetData.birthday.split('-')[0])}}&nbsp;{{targetData.high}}cm&nbsp;{{constellationMap[targetData.constellation]}}
      </p>

      <p class="info-con">
        {{educationMap[targetData.education]}}
        {{targetData.ifStudent ? '' : ''}}{{targetData.school ? `${targetData.school}` : ''}}
      </p>
      <template v-if="!targetData.ifStudent">

        <p class="info-con desc">
          {{targetData.companyVisible && targetData.company ? `${targetData.company}` : ''}}
          <span class="info-con desc" v-if="!targetData.companyVisible">
            {{targetData.industry ? `(${targetData.industry})` : ''}}
          </span>
        </p>
        <p class="info-con desc">
          {{targetData.job}}
        </p>
      </template>
      <p class="info-con">{{statusMap[targetData.singleStatus]}} {{assetsMap[targetData.richesInfo]}}
        {{targetData.annualSalaryVisible && targetData.annualSalary != 0 ? salaryMap[targetData.annualSalary] : ''}}</p>
      <p class="info-con location">
        {{cityText(targetData)}}
        
        {{nativeCityText(targetData)}}
      </p>
    </div>

    <div class="desc-list" v-if="targetData.mid">
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/self-description.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.selfDescription}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/family.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.familyInfo}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/experice.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.personalExperience}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/hobby.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.hobby}}</p>
      </div>
      <!-- <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/economic.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.richesInfo}}</p>
      </div> -->
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/love.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.loveMind}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/require.png" alt="" class="self-description" /></p>
        <p class="desc-detail">{{targetData.ideaPartner}}</p>
      </div>
    </div>

    <div class="btns" v-if="mId != targetData.mid">
      <div class="like-btn btn" :class="{'liked': targetData.like}" @click="clickLike">
        <p class="like word">
          <img v-if="targetData.like" src="~@/assets/img/liked.png" alt="" class="like-icon" />
          <img v-else src="~@/assets/img/like.png" alt="" class="like-icon" />
          {{targetData.like ? 'TA' : 'ta'}}
        </p>
        <p class="remain-quanitity" v-if="userId">{{userData.likeBalance || 0}}</p>
      </div>
      <div class="apply-btn btn" :class="{'got': targetData.wechatPicUrl}" @click="getWebChatCode">
        <p class="word"><img src="~@/assets/img/webchat-logo.png" alt=""
            class="apply-icon" />{{targetData.wechatPicUrl ? 'TA' : ''}}</p>
      </div>
    </div>

    <van-dialog v-model="confirmPurchaseDialog" confirmButtonText="" :show-cancel-button="true" @confirm="purchase">
      <div class="confirm-purchase-content">
        <h3 class="price-con">
          <p class="cur-price">{{transferPrice(purchaseItem.currentPrice)}}<span class="unit">/</span></p>
          <p class="origin-price">{{transferPrice(purchaseItem.originPrice)}}<span class="unit">/</span></p>
        </h3>
        <ul class="tipsList">
          <ol>1.</ol>
          <ol>2.ID</ol>
          <ol>3.Ta</ol>
          <ol>4.</ol>
        </ul>
      </div>
    </van-dialog>

    <van-dialog v-model="isShowCodeDialog" confirmButtonText=""
      :message="overlimit ? '30' : ''">
      <div class="target-code-dialog" v-if="!overlimit">
        <p class="target-name">{{targetData.nickName}}</p>

        <img :src="targetData.wechatPicUrl" alt="" class="code" />
        <p class="tips"><br />
          <template v-if="copyFail"><br>ID</template>
          <template v-else>ID<br /></template>
        </p>

      </div>
    </van-dialog>

    <van-dialog v-model="showErrorDialog" :message="errorDialogContent.message"
      :confirmButtonText="errorDialogContent.confirmText" :show-cancel-button="true" @confirm="handleErrorDialogClose">
    </van-dialog>

    <!--  -->
    <van-popup v-model="isReporting" :close-on-popstate="true" position="right">
      <div class="report-content">
        <div class="title-con">
          <a @click="switchReport" href="javascript:;" class="btn"><img src="~@/assets/img/back-icon.png"
              class="back-icon" /></a>
          <h3 class="title"></h3>
        </div>
        <div class="input-cotent">
          <textarea ref="reportInputContent" class="textarea" placeholder="10"
            v-model="reportData.description"></textarea>
        </div>
        <div class="upload-box">
          <p class="upload-title">1-3</p>
          <div class="upload-content">
            <div class="upload-item" v-for="(img, index) in reportData.imgData" :key="index">
              <img :src="img.url" alt="" class="img-item" />
              <img src="~@/assets/img/close-white.png" alt="" class="delete-btn" @click.stop="deleteImg(index)" />
            </div>
            <div class="upload-item select-item" v-if="reportData.imgData.length < 3">
              <!-- <input type="file" class="upload-input" accept="image/*" ref="file" @change="selectImg" /> -->
              <Oss @fileChange="selectImg" @uploadSucess="uploadSuccess" browse_button="uploadBtn" :actionFile="tempImg"
                :params='coverParams' class="oss-item">
                <div id="uploadBtn">
                  <img src="~@/assets/img/upload-bg.png" class="upload-btn" />
                </div>
              </Oss>
              <img src="~@/assets/img/upload-bg.png" alt="" class="upload-btn" />
            </div>
          </div>
        </div>
        <a href="javascript:;" class="commit-report" :class="{disable: disableReport}" @click="submitReport"></a>
      </div>
    </van-popup>

    <!--  -->
    <AccelerateAduit v-model="isShowAccelerate" :isBaseData="isBaseData" @confirm="isShowAccelerate = false"
      @close="isShowAccelerate = false" />

    <!--  -->
    <purchaseModal :show="isShowModal" @close="isShowModal=false" @callback="isShowModal=false" />

    <van-image-preview v-if="targetData.coverPicUrlList" v-model="isShowPreview" :images="targetData.coverPicUrlList" :start-position="previewIndex" @change="changePreview"></van-image-preview>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
// import { copy, isIOS, transferBytes } from '@/utils/tools'
import wxApi from '@/utils/wxAuth'
import { Toast, Swipe, SwipeItem } from 'vant'
import purchaseModal from '@/components/common/purchaseModal'
import AccelerateAduit from '@/components/dialog/accelerateAduit'
import Oss from '@/components/common/oss'
import { copy, isIOS, transferBytes, watermark } from '@/utils/tools'
import { UPLOAD_TYPE } from '@/config/constant'

const getRotate = () => {
  return import('@/utils/rotateImg.js')
}
export default {
  name: 'detailInfo',
  components: {
    AccelerateAduit,
    purchaseModal,
    Swipe,
    SwipeItem,
    Oss
  },
  data () {
    return {
      targetData: {
        wechatPicUrl: ''
      },
      isReporting: false,
      reportData: {
        description: '',
        imgData: []
      },
      tempImg: null,
      coverParams: { type: UPLOAD_TYPE.report },
      preImgArr: [],
      overlimit: false,
      purchaseItem: {},
      confirmPurchaseDialog: false,
      // 
      isShowCodeDialog: false,
      // 
      errorStatus: 0,
      showErrorDialog: false,
      errorDialogContent: {
        message: '',
        confirmText: ''
      },
      //   
      isShowModal: false,
      // 
      isShowAccelerate: false,
      isBaseData: true,
      // 
      copyFail: false,
      // 
      isShowPreview: false,
      previewIndex: 0
    }
  },
  computed: {
    ...mapState({
      userData: 'userData',
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId',
      mId: 'mId'
    }),
    id () {
      return this.targetData.id
    },
    targetMid () {
      return this.$route.params.id
    },
    disableReport () {
      return this.reportData.description.length < 10 || !this.reportData.imgData.length
    },

    // 
    cityText (item) {
      return (item) => {
        if (!item.cityId) {
          return ''
        }
        if (parseInt(item.cityId) === 900000) return '' + (item.country + ' ' + item.state)
        return `${this.areaData['city_list'][item.cityId]}`
      }
    },
    nativeCityText (item) {
      return (item) => {
        if (!item.nativeCityId) {
          return ''
        }
        let nid = parseInt(item.nativeCityId)
        if (nid === 900000) {
          return item.nativeCountry + ' ' + item.nativeState + ''
        } else {
          const provice = String(nid).substr(0,2) + '0000'
          return `${[110100, 120100, 310100, 500100].indexOf(nid) >= 0 ? '' : `${this.areaData['province_list'][provice]}`}${this.areaData['city_list'][item.nativeCityId]}`
        }
      }
    },
    schoolText (item) {
      return (item) => `${this.educationMap[item.education]}${item.ifStudent ? '()' : ''}`
    }
  },
  watch: {
    isReporting (val) {
      if (val) {
        this.$nextTick(() => {
          let $ref = this.$refs['reportInputContent']
          $ref && $ref.focus()
        })
      }
    },
    isShowCodeDialog (val) {
      this.copyFail = false
      if (val) {
        copy(`${this.mId}-${this.userData.cityId == '900000' ? this.userData.state : this.areaData['city_list'][this.userData.cityId]}-${this.userData.age}-${this.educationMap[this.userData.education]}-${this.userData.ifStudent ? '' : this.userData.job}`,
          this.$refs['code-con']).catch(() => {
            this.copyFail = true
          })
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    // 
    calculater (year) {
      var arr1 = ['', '', '', '', '', '', '', '', '', '', '', '']
      return /^\d{4}$/.test(year) ? `${arr1[year % 12]}` : false
    },
    fetchInfo (notLoad) {
      if (!this.targetMid) {
        return
      }
      !notLoad && this.$root.openLoading()

      this.$http('/api/member/info', {
        targetMid: this.targetMid
      }, 'get').then(res => {
        !notLoad && this.$root.closeLoading()
        if (res.code === '0000') {
          const d = res.data

          this.targetData = d
          wxApi.configShare({
            title: 'Hello',
            // desc: 'ID----',
            desc: `${d.mid}-${this.educationMap[d.education]}-${d.ifStudent ? '' : d.job}-${d.age}-${parseInt(d.cityId) === 900000 ? (d.country + ' ' + d.state) : this.areaData['city_list'][d.cityId]}`,
            link: location.href,
            imgUrl: `${d.headPicUrl}`
          })
        } else {
          this.$toast(res.data.msg)
        }
      }, err => {
        !notLoad && this.$root.closeLoading()
        this.$toast(err)
      })
    },
    transferPrice (val) {
      if (typeof val !== 'number') {
        return 0
      } else {
        return (val / 100).toFixed(1)
      }
    },
    /**
     * @description 
     * @returns { Boolean } truefalse
     */
    checkLogin () {
      if (!this.userId) {
        // 
        this.errorStatus = 0
        this.errorDialogContent.message = ''
        this.errorDialogContent.confirmText = ''
        this.showErrorDialog = true
        return false
      } else {
        return true
      }
    },
    /**
     * @description 
     * @returns { Boolean } truefalse
     */
    checkBaseData () {
      if (!this.checkLogin()) return
      if (parseInt(this.userData.basedataAuth) !== 3) {
        if (parseInt(this.userData.basedataAuth) === 1) {
          this.isBaseData = true
          this.isShowAccelerate = true
        } else if (parseInt(this.userData.basedataAuth) === 0) {
          this.errorStatus = 1
          this.errorDialogContent.message = ''
          this.errorDialogContent.confirmText = ''
          this.showErrorDialog = true
        } if (parseInt(this.userData.basedataAuth) === 2) {
          this.errorStatus = 1
          this.errorDialogContent.message = ''
          this.errorDialogContent.confirmText = ''
          this.showErrorDialog = true
        }
        return
      }
      return true
    },
    /**
     * @description 
     * @returns { Boolean } truefalse
     */
    checkStatus () {
      if (!this.checkBaseData()) return
      // 
      let map = { identityAuth: '', academicAuth: '', workAuth: '' }

      for (let index = 0, keys = Object.keys(map); index < keys.length; index++) {
        let item = keys[index]; let label = map[item]; let option = this.userData[item]
        // 
        if (option === 2) {
          this.errorDialogContent.message = ``
          this.errorDialogContent.confirmText = ''
          this.showErrorDialog = true
          return
        }
        if (option === 0) {
          this.errorStatus = 2
          this.errorDialogContent.message = ''
          this.errorDialogContent.confirmText = ''
          this.showErrorDialog = true
          return
        }
        if (option === 1) {
          this.isBaseData = false
          this.isShowAccelerate = true
          return
        }
      }
      return true
    },
    switchReport () {
      if (!this.checkLogin()) return
      this.isReporting = !this.isReporting
    },
    switchFollow () {
      if (!this.checkLogin()) return
      this.$root.openLoading()
      this.$http(`/api/member/action`, {
        targetMemberId: this.id,
        fromMemberId: this.userId,
        toMemberId: this.id,
        type: this.targetData.follow ? 11 : 10
      }).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.targetData.follow = !this.targetData.follow
          let followCount = parseInt(this.userData.followCount)
          if (this.targetData.follow) {
            this.$toast('')
            followCount = followCount >= 0 ? followCount + 1 : 1
            this.updateUserData({
              followCount
            })
          } else {
            this.$toast('')
            followCount = followCount > 1 ? followCount - 1 : 0
            this.updateUserData({
              followCount
            })
          }
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    clickLike () {
      if (this.targetData.like) {
        return
      }
      if (!this.checkBaseData()) return
      this.$root.openLoading()
      this.$http('/api/member/action', {
        targetMemberId: this.id,
        fromMemberId: this.userId,
        toMemberId: this.id,
        type: 20
      }).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.targetData.follow = true
          // 
          let likeCount = parseInt(this.userData.likeCount)
          likeCount = likeCount >= 0 ? likeCount + 1 : 1
          this.updateUserData({
            likeCount
          })
          //   
          let likeBalance = parseInt(this.userData.likeBalance)
          likeBalance = likeBalance >= 0 ? likeBalance - 1 : 1
          this.updateUserData({
            likeBalance
          })
          if (res.data['wechatPicUrl']) {
            this.targetData.wechatPicUrl = res.data.wechatPicUrl
            this.isShowCodeDialog = true
          } else {
            this.targetData.like = true
            this.$toast('ta')
          }
        } else if (res.code === '200001') {
          // 
          this.isShowModal = true
        } else {
          this.$toast('!')
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    /**
     * @description 
     */
    async getWebChatCode () {
      // this.confirmPurchaseDialog = true

      if (this.targetData.wechatPicUrl) {
        // 
        this.isShowCodeDialog = true
      } else if (!this.checkStatus()) { } else if (this.userData.overlimit) {
        this.overlimit = true
      } else {
        let { code, data, msg } = await this.$http('/api/member/product/list', { type: 2 }, 'get')
        if (!(code === '0000' && data)) return this.$toast(msg)
        // 
        this.purchaseItem = data[0]
        this.confirmPurchaseDialog = true
      }
    },
    /**
     * @description 
     */
    async purchase () {
      try {
        var fd = new FormData()
        fd.append('targetMemberId', this.id)
        fd.append('productId', this.purchaseItem.id)
        let { code, data, msg } = await this.$http('/api/member/purchase/transaction', fd)
        if (!(code === '0000' && data)) {
          return Toast(msg)
        }
        // 
        this.$http('/api/login/wx/jssdk/config/get', {
          pageUrl: location.href
        }, 'get').then(res => {
          if (res.code === '0000') {
            wxApi.init({
              appid: data.appId, // 
              redirectUri: location.href
            }).wexinPay(data, res.data).then(res => {
              if (res.actionCode === 200) {
                // this.$toast('')
                let times = 0
                this.$root.openLoading()
                let timer = setInterval(() => {
                  // 3
                  if (times >= 6 || this.targetData.wechatPicUrl) {
                    clearInterval(timer)
                    this.$root.closeLoading()
                    if (!this.targetData.wechatPicUrl) {
                      this.$toast(', ')
                    } else {
                      this.isShowCodeDialog = true
                    }
                  } else {
                    // fetchInfo 
                    this.fetchInfo(true)
                    times++
                  }
                }, 500)
              } else {
                this.$toast('')
              }
            }).catch(e => {
              Toast('')
            })
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
        })
      } catch (error) {
        Toast(error)
      }
    },
    handleErrorDialogClose () {
      this.showErrorDialog = false
      this.$nextTick(() => {
        switch (this.errorStatus) {
          case 0:
            this.$router.push({
              path: '/account.html',
              query: {
                redirect: this.$route.path
              }
            })
            break
          case 1:
            // 
            if (parseInt(this.userData.basedataAuth) === 1) {
              this.$router.push({
                path: '/account.html'
              })
            } else {
              this.$router.push({
                path: '/baseInfo.html'
              })
            }
            break
          case 2:
            this.$router.push({
              path: '/account.html'
            })
            break
        }
      })
    },
    execCopy (text) {
      copy(text, this.$refs['code-con']).then(res => {
        this.$toast('')
      }, () => {
        this.$toast('')
      })
    },
    getPics () {
      let pics = []
      for (let i = 0; i < this.reportData.imgData.length; i++) {
        pics.push(this.reportData.imgData[i].id)
      }
      return pics
    },
    showPreview (index) {
      this.previewIndex = index
      this.isShowPreview = true
    },
    changePreview (index) {
      this.$refs['swipe'].swipeTo(index)
    },
    submitReport () {
      if (this.disableReport) return
      let fd = {}
      fd.targetMemberId = this.id
      fd.message = this.reportData.description
      fd.pics = this.getPics()
      this.$root.openLoading()
      this.$http('/api/member/report', fd).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.$toast('')
          setTimeout(() => {
            this.isReporting = false
          }, 1000)
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    selectImg (e) {
      this.tempImg = e.file
    },
    uploadSuccess (resUpload) {
      let imgReport = {}
      imgReport.id = resUpload.picId
      imgReport.url = resUpload.url
      this.reportData.imgData.push(imgReport)
      this.tempImg = null
    },
    deleteImg (index) {
      this.reportData.imgData.splice(index, 1)
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.fetchInfo()
    })
  },
  beforeRouteLeave (to, from, next) {
    this.targetData = {}
    wxApi.configShare()
    next()
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/constant.scss";
.detail-info-container {
  padding: 30px 40px 180px;
  .img-swiper {
    position: relative;
    height: 470px;
    border-radius: 8px;
    overflow: hidden;
  }
  .img-box {
    height: 100%;
    .van-swipe__indicators {
      left: auto;
      right: 40px;
    }
    .van-swipe__indicator {
      width: 8px;
      height: 8px;
      border-radius: 8px;
      opacity: 1;
      background-color: #fff;
    }
    .van-swipe__indicator--active {
      width: 30px;
      background-color: $mainColor !important;
    }
  }

  .info-box {
    padding-bottom: 20px;
    border-bottom: 1px solid #e0e0e0;
  }
  .name-op-con {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
  }
  .name-con {
    font-size: 32px;
    line-height: 44px;
    font-weight: bold;
    .nick {
      overflow: hidden;
      font-size: 38px;
      line-height: 44px;
      text-align: center;
      display: inline-block;
      max-width: 200px;
      white-space: nowrap;
      text-overflow: ellipsis;
      vertical-align: text-bottom;
    }
    .sex-icon {
